}
.centerarea::after{
  content: '';
  /* position: relative; */
  background: #282828;
  /* inset: 4px; */
  border-radius: 16px;
}
@keyframes animate{
  0%{
    transform: rotate(0deg);
  }
    100% {
      transform: rotate(350deg);
    }
}

